.container {
    position:relative;
    display: flex;
    flex-direction: column;
    padding: 0;
    width: 640px;
    height: auto;
    left: 50%;
    /* 左边缘居中 */
    margin-left: -320px;
    /* 左移自身宽度的一半 */
    top: 100px;
}

.item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 50px;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 540px;
    height: auto;
    background-color: rgba(255, 255, 255, 0.9);
    border-style: solid;
    border-color: #000000;
    box-shadow: 4px 4px 0px 2px rgba(230, 0, 18, 0.9);
    border-radius: 12px;
    margin-top: 50px;
    position: relative;
}

.hint {
    position: relative;
    font-family: 'PublicPixel';
    font-size: 24px;
    height: auto;
    max-width: 577px;
    font-weight: bold;
    color: rgba(230, 0, 18, 0.9);
    background-image: linear-gradient(to left, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.9) 50%, rgba(230, 0, 18, 0.9) 50%, rgba(230, 0, 18, 0.9) 100%);
    background-size: 200% 100%;
    background-position: right;
    display: inline-block;
    transition: background-position 0.5s, color 0.5s;
    letter-spacing: 0.1em;
    margin: 16px 0px 16px 0px;
}

.item:hover .hint {
    background-position: left;
    color: white;
}

.text {
    font-size: 14px;
    height: auto;
    max-width: 577px;
    font-family: 'Taipei Sans TC Beta';
    line-height: 24px;
}