
.article {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 10px 0px 10px 0px;
    font-family: 'Taipei Sans TC Beta';
}

.article .title {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 20px;
}

.article .title a {
    text-decoration: none;
    color: inherit;
    font-style: normal;
    font-size: 20px;
    font-weight: bold;
    line-height: 16px;
    font-family: 'Taipei Sans TC Beta';

}

.article .title a:hover{
    color: #3370FF;
}

.article .label {
    display: inline;
    position: relative;
    box-sizing: border-box;
    width: auto;
    height: auto;
    border-radius: 4px;
    font-size: 12px;
    font-family: 'Taipei Sans TC Beta';
    font-weight: bold;
    color: white;
    padding: 3px 8px;
}
.article .l1{
    background: #3370FF;
}

.article .l2{
    background: #11be45;
}


.article .preview{
    display: flex;
    flex-direction: row;
    gap: 20px;
}

.article .preview img {
    width: 236px;
    height: 143px;
}

.article .preview .summary {
    font-size: 14px;
    font-family: 'Taipei Sans TC Beta';
    color: #3b393a;
    line-height:24px;
}

hr{
    margin: 20px 0px 20px 0px;
    color: #3b393a;
}