.down_bt {
    box-sizing: border-box;
    /* Auto layout */
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 7px 10px;
    gap: 5px;
    background: #E60012;
    border: 1px solid #FFFFFF;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
    border-radius: 12px;

}

.down_bt p {
    width: auto;
    height: 16px;
    margin-top: 0;
    margin-bottom: 0;
    font-family: 'Taipei Sans TC Beta';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    /* identical to box height */
    color: #FFFFFF;
    /* Inside auto layout */
    flex: none;
    order: 1;
    flex-grow: 0;
}

.down_bt img {
    width: 20px;
    height: 20px;
    color: rgba(255, 255, 255, 0.9);
    background: rgba(255, 255, 255, 0);
    /* Inside auto layout */
    flex: none;
    order: 0;
    flex-grow: 0;
}

.down_bt:active {
    transform: translateY(2px);
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
    background-color: #D10000;
  }