.top-n-b {
    position: absolute;
    /* 将元素的定位方式设置为绝对定位 */
    top: 50px;
    left: 50%;
    /* 将元素的左侧位置设置为页面的中间 */
    transform: translate(-50%, -50%);
    /* 使用 transform 属性和 translate() 函数将元素向上和向左移动一定的距离，从而实现垂直居中 */
    width: 640px;
    height: 50px;

    /* 其他样式属性 */
    box-sizing: border-box;
    background: linear-gradient(90.35deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
    border-width: 1.5px 2px 2px 1.5px;
    border-style: solid;
    border-color: #000000;
    box-shadow: 4px 4px 0px 2px rgba(230, 0, 18, 0.9);
    border-radius: 12px;
}


.welcome {
    position: absolute;
    /* 将元素的定位方式设置为绝对定位 */
    left: 50px;
    font-family: 'Taipei Sans TC Beta';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 12px;
    /* identical to box height */
    color: #000000;
}


.avatar {
    position: absolute;
    width: 30px;
    height: 30px;
    left: 431px;
    border-radius: 50px;
}

.name {
    position: absolute;
    width: auto;
    height: 12px;
    left: 471px;
    font-family: 'Taipei Sans TC Beta';
    font-style: normal;
    font-weight: 700;
    line-height: 12px;

    color: #000000;
}

.y-center {
    top: 50%;
    /* 将元素的顶部位置设置为页面的中间 */
    transform: translateY(-50%);
    /* 使用 transform 属性和 translateY() 函数将元素向上移动一定的距离，从而实现垂直居中 */
}

.x-center {
    left: 50%;
    /* 将元素的顶部位置设置为页面的中间 */
    transform: translate(-50%);
    /* 使用 transform 属性和 translateY() 函数将元素向上移动一定的距离，从而实现垂直居中 */
}

.mail {
    position: absolute;
    margin: auto;
    left: 541px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;  
}

.mail img {
    width: 24px;
    height: 24px;
    margin: auto;
}

.top-n-b a {
    text-decoration: none;
    color: inherit;

}