/* 默认样式 */
.box {
    position: fixed;
    top: 170px;
    left: 150px;
    width: 120px;
    height: auto;
    text-align: center;
  }
  
  .box a {
    display: block;
    color: #000;
    font-size: 16px;
    text-decoration: none;
    height: 30px;
    line-height: 30px;
    padding: 10px;
    border: 2px solid rgba(0, 0, 0, 0);
  }
  
  .box a:hover {
    background-color: #fff;
    border: 2px solid #000000;
    box-shadow: 4px 4px 0px 2px rgba(230, 0, 18, 0.9);
    border-radius: 8px;
    padding: 10px;
    font-weight: bold;
  }
  
  /* 媒体查询 */
  @media screen and (max-width: 768px) {
    .box {
      top: 50px;
      left: 50px;
      width: 80px;
    }
  
    .box a {
      font-size: 12px;
    }
  }
  